<template>
<div>
  <div class="left">
    <weather-widget v-if="lat && long" api-key="eb9331a6725795f350eee67de044d0d6" title="Weather" bar-color="#3f455b" v-bind:latitude="lat" v-bind:longitude='long' language="en" units="us" v-bind:hide-header="true">
    </weather-widget>
  </div>
  <div class="right virgil">
    <p>WEBAPP c/o DILLON CULP & GABRIEL GUZMAN</p>
    <p>"NEW TAB PAGE"</p>
    <p>GAINESVILLE, FLORIDA</p>
    <p>Navigate across the web with a single command</p>
    <p>Enter "help" to open command line documentation</p>
  </div>
</div>
</template>

<script>
import VueWeatherWidget from 'vue-weather-widget';
import 'vue-weather-widget/dist/css/vue-weather-widget.css';

export default {
  name: 'Weather',
  components: {
    'weather-widget': VueWeatherWidget
  },
  data() {
    return {
      lat: null,
      long: null
    }
  },
  methods: {
    locationSuccess(position) {
      this.lat = position.coords.latitude.toString();
      this.long = position.coords.longitude.toString();
    }
  },
  created() {
    console.log('a');
    if (navigator.geolocation) {
      navigator.geolocation.getCurrentPosition(this.locationSuccess);
    }
    // Get latitude and longitude;
  }
}
</script>

<style>
.left {
  float: left;
  width: 50%;
  color: #575f7e;
}

.right {
  float: right;
}

.virgil {
  width: 25%;
}

.virgil>p {
  margin: 0;
  font-size: 15px;
  text-align: right;
}
</style>



