import eventBus from '@/assets/js/eventBus.js'
import manager from '@/assets/js/storageManager.js'

export const defaultAliasesV2 = {
  amazon: (args) => {
    switch (args[1]) {
      case 'cart':
        return 'https://www.amazon.com/gp/cart/view.html';
      case 'order':
        return 'https://www.amazon.com/gp/css/order-history';
      default:
        return redirect('https://www.amazon.com', '/s/?field-keywords=', args)
    }
  },
  g: (args) => { return redirect('https://www.google.com/', 'search?q=', args) },
  dg: (args) => { return redirect('https://duckduckgo.com/', '?q=', args) },
  r: (args) => {
    switch (args.length) {
      case 1:
        return redirect('https://reddit.com', undefined, args);
      case 2:
        return redirect(`https://reddit.com/r/${args[1]}`, undefined, [args[0]]);
      case 3:
        return redirect(`https://reddit.com/${(args[1] !== 's') ? `r/${args[1]}/` : ''}`, 'search?q=', args.slice(1)) + `${(args[1] === 's') ? '' : '&restrict_sr=on'}`;
      default:
        return redirect(`https://reddit.com/${(args[1] !== 's') ? `r/${args[1]}/` : ''}`, 'search?q=', args.slice(1)) + `&sort=${args[3]}${(args[1] === 's') ? '' : '&restrict_sr=on'}`;
    }
    // TODO add date range
    // TODO add function to sort subreddit
  },
  4: (args) => {
    switch (args.length) {
      case 1:
        return 'http://www.4chan.org/';
      case 2:
        return `http://boards.4chan.org/${args[1]}/catalog`;
      default:
        return `http://boards.4chan.org/${args[1]}/catalog#s=${encodeURIComponent(args[2])}`;
    }
  },
  bit: (args) => { return redirect('https://bitbucket.org/', 'search?q=', args) },
  cr: (args) => { return redirect('http://www.crunchyroll.com/', 'search?q=', args) },
  gmail: (args) => { return redirect('https://mail.google.com/mail/u/0/', '#search/', args) }, // TODO More webmail integrations?
  fb: (args) => { return redirect('https://www.facebook.com/', 'search/top/?q=', args) },
  git: (args) => { return redirect('https://github.com/', 'search?q=', args) },
  hb: (args) => { return redirect('https://hypebeast.com/', 'search?s=', args) },
  i: (args) => { return redirect('https://imgur.com/', 'search?q=', args) },
  mp: (args) => {
    if (args[1] === 'bm') {
      return 'https://my.mangapark.me/bookmark';
    } else {
      return redirect('https://mangapark.me/', 'search?q=', args);
    }
  },
  rym: (args) => {
    switch (args.length) {
      case 1:
        return 'https://rateyourmusic.com/';
      case 2:
        if (args[1] === 'chart') {
          return 'https://rateyourmusic.com/customchart';
        }
        return redirect('https://rateyourmusic.com/', 'search?searchtype=a&searchterm=', args);
      default:
        switch (args[1]) {
          case 'chart':
            args.shift();
            return redirect('https://rateyourmusic.com/', 'customchart?year=', args);
          case 'release':
            args.shift();
            return redirect('https://rateyourmusic.com/', 'search?searchtype=l&searchterm=', args);
          case 'label':
            args.shift();
            return redirect('https://rateyourmusic.com/', 'search?searchtype=b&searchterm=', args);
          case 'cat':
            args.shift();
            return redirect('https://rateyourmusic.com/', 'search?searchtype=j&searchterm=', args);
          case 'genre':
            args.shift();
            return redirect('https://rateyourmusic.com/', 'search?searchtype=g&searchterm=', args);
          default:
            args.shift();
            return redirect('https://rateyourmusic.com/', 'search?searchtype=a&searchterm=', args);
        }
    }
  },
  srk: (args) => { return redirect('http://shoryuken.com/', '?s=', args) },
  sc: (args) => {
    switch (args[1]) {
      case 'likes':
        return 'https://soundcloud.com/you/likes';
      case 'charts':
        return 'https://soundcloud.com/charts/top';
      default:
        return redirect('https://soundcloud.com/', 'search?q=', args);
    }
  },
  so: (args) => { return redirect('https://stackoverflow.com/', args.length ? 'search?q=' : '', args) },
  tblr: (args) => {
    switch (args[1]) {
      case 'blog':
        return `http://${args[2]}.tumblr.com/`;
      default:
        return redirect('https://www.tumblr.com/', 'search/', args);
    }
  },
  tw: (args) => { return redirect('https://twitter.com/', 'search?q=', args) },
  yt: (args) => {
    if (args[1] === 's') {
      return 'https://www.youtube.com/feed/subscriptions'
    }
    return redirect('https://www.youtube.com/', 'results?search_query=', args);
  },
  gm: (args) => { return redirect('https://www.google.com/maps/', 'search/?api=1&query=', args) },
  wa: (args) => { return redirect('https://www.wolframalpha.com/', 'input/?i=', args) },
  wiki: (args) => { return redirect('https://en.wikipedia.org/', 'wiki/Special:Search?search=', args) },
  def: (args) => { return redirect('http://www.dictionary.com/', 'browse/', args) },
  syn: (args) => { return redirect('http://www.thesaurus.com/', 'browse/', args) },
  netflix: (args) => { return redirect('https://www.netflix.com/', 'search?q=', args) },
  imbd: (args) => { return redirect('http://www.imdb.com/', 'find?q=', args) },
  set: {
    subpanel: () => { eventBus.$emit('sub_panel'); return 'Removed panel' },
    addpanel: () => { eventBus.$emit('add_panel'); return 'Added panel' },
    default: (args) => {
      // Get cliConfig
      const config = manager.getCliConfig();
      if (args[2]) {
        // Change defaultCommand
        config.defaultCommand = args[2];
        // Commit to storage
        manager.attachCliConfig(config);
        manager.loadToStorage();
        return `Set default command to ${args[2]}`
      }
      return `Default command set to: ${config.defaultCommand}`
    }
  },
  help: () => { return 'https://github.com/Gabriel-Guzman/newtab_page/blob/master/docs/cliCommands.md' }
}

function redirect(url, searchKey, args, type) {
  console.log(`args length: ${args.length}`);
  switch (args.length) {
    case 1:
      console.log(`return url ${url}`);
      return url;
    default:
      console.log(`return search url`);
      return `${url}${searchKey}${(type) ? args[1].replace(/ /g, '+') : encodeURIComponent(args[1])}`;
  }
}



