<template>
<div class="container">
  <div class="history-container">
    <ul id="example-1">
      <li class="fade" v-if="history[history.length - 5]">
        {{ history[history.length - 5] }}
      </li>
      <li v-if="history[history.length - 4]">
        {{ history[history.length - 4] }}
      </li>
      <li v-if="history[history.length - 3]">
        {{ history[history.length - 3] }}
      </li>
      <li v-if="history[history.length - 2]">
        {{ history[history.length - 2] }}
      </li>
      <li v-if="history[history.length - 1]">
        {{ history[history.length - 1] }}
      </li>
    </ul>
  </div>
  <div class="cli-container">
    <form v-on:submit="runCliV2($event)">
      <span>></span><input v-model="command" type="text" />
    </form>
  </div>
  <span class="time">{{ computedTime }}</span>
</div>
</template>

<script>
import validUrl from 'valid-url'

import manager from '@/modules/storageManager'
import eventBus from '@/modules/eventBus'
import { defaultAliases } from '@/modules/aliases/aliases.js'
import { defaultAliasesV2 } from '@/modules/aliases'

export default {
  name: 'CommandLine',
  data () {
    return {
      command: '',
      time: new Date(),
      defaultAliases,
      config: manager.getCliConfig(),
      history: []
    }
  },
  created() {
    this.attachToEventBus();
    console.log(this);
    const self = this;
    setInterval(function() {
      self.time = new Date()
    }, 1000)
  },
  computed: {
    computedTime() {
      return `${this.time.getHours() % 12}:${(String(this.time.getMinutes()).length === 2) ? this.time.getMinutes() : '0' + this.time.getMinutes()}${(this.time.getHours() > 12) ? 'pm' : 'am'}`
    },
    customAliases() {
      return (manager.getCliConfig().aliases) ? manager.getCliConfig().aliases : false;
    }
    // computedDefaultCommand() {
    //   return this.defaultAliases.length;
    //   for (var i = 0; i < this.defaultAliases.length; i++) {
    //     const alias = this.defaultAliases[i];
    //     if (alias.alias === this.config.defaultCommand) {
    //       return alias;
    //     }
    //   };
    // }
  },
  methods: {
    attachToEventBus() {
      eventBus.$on('commit', () => {
        // attaching to manager
      })
    },
    runCliV2(e) {
      e.preventDefault();
      const args = this.command.split(';');
      this.history.push('>' + this.command);
      // Clear cli once args are captured
      this.command = '';
      // Check if last arg is new tab flag
      const newTab = (args[args.length - 1] === 'n');
      // Remove new tab arg
      if (args.length > 1 && args[args.length - 1] === 'n') {
        console.log(`removing new tab key`)
        args.pop();
      }
      // Get array of all defaultAliases
      const aliases = Object.keys(defaultAliasesV2);
      console.log(`Aliases`, aliases);
      // Get array of all setting aliases
      const setAliases = Object.keys(defaultAliasesV2.set);
      // Check for and handle set commands
      if (args[0] === 'set' && setAliases.includes(args[1])) {
        this.history.push(defaultAliasesV2.set[args[1]](args));
      } else if (aliases.includes(args[0])) { // Check if first arg is in aliases
        // Open in new tab if newTab === true
        if (newTab) {
          const navURL = defaultAliasesV2[args[0]](args);
          this.history.push(`Opening ${navURL} in new tab`);
          window.open(navURL);
        } else {
          const navURL = defaultAliasesV2[args[0]](args);
          this.history.push(`Navigating to ${navURL}`);
          window.location.href = navURL;
        }
      } else {
        // Navigate to URL if first arg is url
        if (validUrl.isUri(args[0])) {
          // Add protocal if not included in arg
          if (args[0].slice(0, 7) !== 'http://' && args[0].slice(0, 8) !== 'https://') {
            args[0] = `https://${args[0]}`;
          }
          // Open first arg in new tab if new tab flag included
          if (newTab) {
            this.history.push(`Opening ${args[0]} in new tab`)
            window.open(args[0]);
          } else {
            this.history.push(`Navigating to ${args[0]}`)
            window.location.href = args[0];
          }
        } else { // Search with default command if first arg is not an alias, setting or url
          // Add default command to args
          args.unshift('g');
          console.log(args);
          // Open in new tab if new tab flag included
          if (newTab) {
            const navURL = defaultAliasesV2[args[0]](args);
            this.history.push(`Opening ${navURL} in new tab`);
            window.open(navURL);
          } else {
            const navURL = defaultAliasesV2[args[0]](args);
            this.history.push(`Navigating to ${navURL}`);
            window.location.href = defaultAliasesV2[args[0]](args);
          }
        }
      }
    }
    // runCli(e) {
    //   e.preventDefault();
    //   const args = this.command.split(';');
    //   const validUrl = checkUrl(args[0], (args[1] === 'n'));
    //   console.log(validUrl);
    //   if (!validUrl) {
    //     switch (args[0]) {
    //       case 'subpanel':
    //         eventBus.$emit('sub_panel');
    //         break;
    //       case 'addpanel':
    //         eventBus.$emit('add_panel');
    //         break;
    //       case 'save':
    //         // Tells all rendered components to attach to storage manager
    //         eventBus.$emit('commit');
    //         manager.loadToStorage();
    //         console.log('Saved to local storage');
    //         break;
    //       case 'set':
    //         switch (args[1]) {
    //           case 'defaultCommand':
    //             if (args[2]) {
    //               this.config.defaultCommand = args[2];
    //               manager.attachCliConfig(this.config);
    //               manager.loadToStorage();
    //               manager.loadFromStorage();
    //               this.command = '';
    //             }
    //             break;
    //           default:
    //             break;
    //         }
    //         break;
    //       default:
    //         var navigated = false
    //         if (args[0].includes(' ')) {
    //           checkUrl(`${this.computedDefaultCommand.url}${this.computedDefaultCommand.searchPattern}${args[0].replace(/ /g, this.computedDefaultCommand.space)}`, args[1] === 'n');
    //         } else {
    //           console.log(this);
    //           for (var i = 0; i < this.defaultAliases.length; i++) {
    //             const alias = this.defaultAliases[i];
    //             if (alias.alias === args[0]) {
    //               console.log(`alias found ${alias.alias}`);
    //               if (args[1]) {
    //                 if (args[1] !== 'n') {
    //                   navigated = true;
    //                   checkUrl(`${alias.url}${alias.searchPattern}${args[1].replace(/ /g, alias.space)}`, args[2] === 'n');
    //                   break;
    //                 } else {
    //                   navigated = true;
    //                   checkUrl(alias.url, true);
    //                   break;
    //                 }
    //               } else {
    //                 console.log(`no arg 2 navigating to ${alias.url}`);
    //                 navigated = true;
    //                 checkUrl(alias.url, false);
    //                 break;
    //               }
    //             }
    //           };
    //           if (this.customAliases) {
    //             // this.customAliases.forEach((alias) =>
    //             for (var j = 0; j < this.customAliases.length; j++) {
    //               const alias = this.customAliases[j];
    //               if (alias.alias === args[0]) {
    //                 if (args[1]) {
    //                   if (args[1] !== 'n') {
    //                     navigated = true;
    //                     checkUrl(`${alias.url}${alias.searchPattern}${args[1].replace(/ /g, alias.space)}`, args[2] === 'n');
    //                     break;
    //                   } else {
    //                     navigated = true;
    //                     checkUrl(alias.url, true);
    //                     break;
    //                   }
    //                 } else {
    //                   navigated = true;
    //                   checkUrl(alias.url, false);
    //                   break;
    //                 }
    //               }
    //             };
    //           }
    //           if (!navigated) {
    //             checkUrl(`${this.computedDefaultCommand.url}${this.computedDefaultCommand.searchPattern}${args[0].replace(/ /g, this.computedDefaultCommand.space)}`, args[1] === 'n');
    //           }
    //         } // window.location.replace(args[0]);
    //     }
    //   }
    // }
  }
}
</script>

<style>
* {
  font-size: 35px;
  font-family: 'Roboto Mono', monospace;
  color: #ccc;
;
}

div.history-container * {
  font-size: 12px;
  text-align: left;
  margin: 0;
  text-decoration: none;
  list-style-type: none;
}

li.fade {
  -webkit-mask-image: linear-gradient(to bottom, transparent 60%, black 70%);
  mask-image: linear-gradient(to bottom, transparent 60%, black 70%);
}

div.history-container>ul {
  padding-left: 22px;
}

div.container {
  margin-top: 15px;
  /* display: flex; */
  position: fixed;
  bottom: 0;
  width: 100%;
}

span.time {
  position: fixed;
  right: 5px;
  font-size: 25px;
  bottom: 0px;
}

div.cli-container {
  float: left;
  width: 80%;
}

input {
  font-size: 18px;
  border: none;
  width: 80%;
  float: left;
  background-color: #212328;
  color: #ccc;
}

div.cli-container span {
  float: left;
}

span {
  font-size: 18px;
  padding: 3px 5px 0.6px 0;
  margin-left: 5px;
}
</style>



