<template>
  <div>
    <modules v-if="config != null" :config="config"/>
    <weather />
    <command-line />
  </div>
</template>

<script>
import CommandLine from './CommandLine';
import Weather from './Weather'
// import Modules from './Modules';
import manager from '@/assets/js/storageManager.js'
import eventBus from '@/assets/js/eventBus.js'

export default {
  name: 'main',
  data () {
    return {
      config: null
    }
  },
  methods: {
    attachToEventBus () {
      eventBus.$on('commit', () => {
        if (this.config) {
          manager.attachConfig(this.config);
        }
      });

      eventBus.$on('add_panel', () => {
        if (this.config) {
          this.config.numPanels++;
        }
      });

      eventBus.$on('sub_panel', () => {
        if (this.config) {
          if (this.config.numPanels > 0) {
            this.config.numPanels--
          }
        }
      });
    }
  },
  components: {
    'command-line': CommandLine,
    weather: Weather
  },
  created () {
    this.config = manager.loadFromStorage().config;
    this.attachToEventBus();
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h1, h2 {
  font-weight: normal;
}
ul {
  list-style-type: none;
  padding: 0;
}
li {
  display: inline-block;
  margin: 0 10px;
}
a {
  color: #42b983;
}
</style>



